<!--  协议详情 -->
<template>
  <div class="table-detail-box agreement-detail">
   <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isCustom='false'></Breadcrumb>
       <div class="rightbox">
        <div class="item" @click="handleEdit()">
           <cc-svg-icon icon-class="Dedit"></cc-svg-icon>
           <span>修改协议</span>
        </div>
        <div class="item" @click="handlePrint()">
           <cc-svg-icon icon-class="print"></cc-svg-icon>
           <span>打印协议</span>
        </div>
      </div>
    </div>
    <!-- 表格 -->
    <div class="inner-box">
      <table class="default-table agreement-table" cellspacing="0" cellspadding="0">
        <tr>
          <th>协议标题</th>
          <td>{{agreementInfo.agreementName||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>机构</th>
          <td>{{agreementInfo.registerOrganization||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>是否甲方</th>
          <td>{{agreementInfo.teamIsFirstStr||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>协议类型</th>
          <td>{{agreementInfo.agreementTypeStr||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>协议内容</th>
          <td class="td-nrbox" ref="agreementInfo">
            <!-- 协议内容 -->
            <div class="agreement-info">
              <h3 class="agreement-title">{{agreementInfo.agreementName||EMPTYTEXT}}</h3>
              <!-- 甲方 -->
              <div class="item-box">
                <div class="intro">
                  <p>甲方（家庭医生团队）：<span></span></p>
                  <p>电话：<span></span></p>
                </div>
                <table cellspacing="0" cellspadding="0">
                  <thead>
                    <tr>
                      <th>团队主要人员姓名</th>
                      <th>岗位</th>
                      <th>职称</th>
                      <th>联系电话</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item,index) in 3" :key="index">
                      <td v-for="(it,i) in 4" :key="i"></td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <!-- 乙方 -->
              <div class="item-box">
                <div class="intro">
                  <p>乙方（居民）</p>
                </div>
                <table cellspacing="0" cellspadding="0" class="resident-box">
                  <thead>
                    <tr>
                      <th>姓名</th>
                      <th>社保（医保）卡号</th>
                      <th>性别</th>
                      <th>年龄</th>
                      <th>家庭电话</th>
                      <th>手机</th>
                      <th>住址</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td v-for="(it,i) in 7" :key="i"></td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <!-- 协议内容 -->
              <div class="agreement-content">
                {{agreementInfo.agreementContent}}
              </div>
              <!-- 协议尾部 -->
              <div class="agreement-footer">
                <div class="leftbox">
                  <P>甲方团队长签字：</P>
                  <P>（单位盖章）</P>
                  <P>日期：<span></span>年<span></span>月<span></span>日</P>
                </div>
                <div class="rightbox">
                  <P>乙方：</P>
                  <P>日期：<span></span>年<span></span>月<span></span>日</P>
                </div>
              </div>
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import printMethod from '../../../util/printMethod' //打印方法
export default { 
  name:'agreement-detail',
  data() {
    return {
      agreementInfo:{} //协议信息
    }
  },
  created() {
    this.getDetailInfo() //获取协议信息
  },
  methods: {
    //修改协议
    handleEdit(id,name){
      this.$router.push({
        path: '/system-manage/agreement-manage/add',
        query:{id:this.$route.query.id}          
      })
    },
    //获取协议信息
    getDetailInfo(){
      this.$emit("viewLoad", true);
      this.$http.get(process.env.VUE_APP_API + '/family/agreementTemplate/getByTemplateId',{
        templateId:this.$route.query.id
      }).then(res => {
        if (res.code===200) {
          this.agreementInfo = res.data
        } else {
          this.$message.error(res.msg);
        }
        this.$emit("viewLoad", false);
      })
      .catch(error => {
        this.$emit("viewLoad", false);
        this.$message.error(error);
      });
    },
    //打印协议
    handlePrint() {
      this.$confirm('确定要打印该协议吗？', '提示', {
        confirmButtonText: '确 认',
        cancelButtonText: '取 消',
        customClass:'infoMessage',
        center:true
      }).then(() => {
        printMethod(this.$refs['agreementInfo'], '签约协议')
          let loading = this.$loading()
          setTimeout(() => {loading.close()}, 800)
      })
    }
  }
};
</script>

<style lang="scss" scoped>
@import "~@/assets/styles/table-default"; 
  .agreement-detail{
    .inner-box{
      .agreement-table{
        width:80%;
        margin:0px auto;
        .td-nrbox{
          text-align:initial;
          padding:30px;
          line-height:30px;
        }
      }
    }
  }
   /* 协议内容 */
  .agreement-info{
     line-height:30px;
    .agreement-title{
      font-size:20px;
      margin-bottom:30px;
      text-align:center;
      font-weight:bold;
    }
    .item-box{
      .intro{
        display:flex;
        align-content:space-between;
        p:first-child{
          width:70%;
        }
        p:last-child{
          width:30%;
        }
      }
      table{
        width:100%;
        border-right:1px solid #f4ebe3;
        border-top:1px solid #f4ebe3;
        margin:15px 0px 30px;
        table-layout:fixed;
        tr,th,td{
          border-left:1px solid #f4ebe3;
          border-bottom:1px solid #f4ebe3;
        }
        th{
          background-color:#fcf7ed;
          padding:0px !important;
          height:40px;
        }
        tbody{
          td{
            line-height:35px;
            height:35px;
            padding:0px !important;
          }
        }
        &.resident-box{
          table-layout:initial;
          th,td{
            &:first-child{
              width:80px;
            }
           &:nth-child(5),&:nth-child(6){
              width:100px;
            }
            &:nth-child(2){
              width:140px;
            }
            &:nth-child(3),&:nth-child(4){
              width:60px;
            }
          }
        }
      }
    }
    .agreement-footer{
      display:flex;
      padding-top:30px;
      .leftbox{
        flex:1;
      }
      .rightbox{
        width:30%;
      }
      p{
        span{
          width:35px;
          display: inline-block;
        }
      }
    }
  }
</style>
